<script>
 export default{
   data () {
       return{
           datas:[
               {id:1,name:"zhangsan",age:12,classes:"一班",project:"数学",score:100}
           ]
       }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
  th{
    text-align: center;
  }
</style>



