<template>
  <div id="app">
    <!--//vue 渐进式框架 动画-->
    <div class="container header">
      <header>
        <div class="title">
          <a href="/" class="glyphicon glyphicon-home"></a>
          学生成绩管理表
        </div>
        <div class="login">
          欢迎XXX登录
          <span @click="logout">退出</span>
        </div>
      </header>
    </div>
    <transition>
         <router-view></router-view>
    </transition>
  </div>
</template>

<script >
export default {
  name: 'App',
    methods:{
      logout(){
          localStorage.remo
      }
    }
}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /*margin-top: 60px;*/
  }

</style>



